import styled from "styled-components";
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { InnerWrap, Text, Title2 } from "../../assets/styles/component";

import Format from "../../assets/images/Formats.webp";
import Logo_img_1 from "../../assets/images/intro_Img_1.webp";
import Logo_img_2 from "../../assets/images/intro_Img_2.webp";
import Logo_img_3 from "../../assets/images/intro_img_3.webp";
import Logo_img_4 from "../../assets/images/intro_img_4.webp";

import { useEffect, useRef, useState } from "react";

const ScrollWrapper = styled.div`
  height: ${({ height }) => height + 1000}px;
  width: 100vw;
  display: flex;
`;

const SFormats = styled.section`
  background: radial-gradient(
      56.92% 42.11% at 111.57% 109.48%,
      #06a5d8 20.47%,
      rgba(0, 51, 233, 0.637795) 59.04%,
      rgba(41, 1, 157, 0) 100%
    ),
    radial-gradient(
      71.73% 87.75% at -17.08% -20.39%,
      #ecbbc3 0%,
      rgba(200, 62, 198, 0.637795) 57.81%,
      rgba(160, 9, 230, 0) 100%
    ),
    #29009c;

  padding: 70px 0;
  border-radius: 30px;
  /* top: 110px; */

  left: 0;
  width: 100vw;
  height: fit-content;
  z-index: 100;

  & > div {
    display: flex;
    flex-direction: column;
    align-items: center;

    & > p {
      text-align: center;
      margin-bottom: 43px;
    }
  }
`;

const Title = styled(Title2)`
  margin-bottom: 40px;
  font-size: 26px;
`;

const FormatList = styled.div`
  position: relative;
  display: flex;
  justify-content: flex-start;
  width: 760px;
  align-self: flex-start;
  transition: 0.1s linear;
  gap: 27px;
  margin-bottom: 70px;
  height: 235px;

  overflow-x: hidden;
  gap: 50px;
`;

const Frame = styled.div`
  margin: 0 auto;
  overflow: hidden;
  outline: 9px solid rgba(255, 255, 255, 0.1);
  border-radius: 30px;

  width: 317px;
  height: 200px;
  min-width: 317px;
  min-height: 200px;

  background: radial-gradient(
    240.7% 80.8% at 107.77% 65.48%,
    #a927ca 0%,
    #d72f75 41.67%,
    #e79079 76.04%,
    #e3592f 98.44%
  );

  img {
    width: 100%;
    margin-bottom: -5px;
  }
`;

const Button = styled.button`
  border: 0;
  cursor: pointer;
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  text-align: center;
  letter-spacing: 0.01em;
  color: #29009c;
  background: #ffffff;
  outline: 10px solid rgba(255, 255, 255, 0.1);
  border-radius: 73px;
  padding: 20px 60px;
  transition: 0.25s ease-in-out;

  &:hover {
    transform: scale(1.01);
    outline: 14px solid rgba(255, 255, 255, 0.1);
  }
`;

const Formats = ({ language }) => {
  const list = useRef(null);
  const formats = useRef(null);

  return (
    <ScrollWrapper>
      <SFormats ref={formats}>
        <InnerWrap>
          <Title>Доступные форматы</Title>

          <FormatList ref={list} className="sticky-container">
            <Frame>
              <LazyLoadImage src={Format} alt="Format_1" loading="lazy" />
            </Frame>
            <Frame>
              <LazyLoadImage src={Logo_img_1} alt="Format_2" loading="lazy" />
            </Frame>
            <Frame>
              <LazyLoadImage src={Format} alt="Format_3" loading="lazy" />
            </Frame>
            <Frame>
              <LazyLoadImage src={Logo_img_2} alt="Format_1" loading="lazy" />
            </Frame>
            <Frame>
              <LazyLoadImage src={Format} alt="Format_2" loading="lazy" />
            </Frame>
            <Frame>
              <LazyLoadImage src={Logo_img_3} alt="Format_3" loading="lazy" />
            </Frame>
            <Frame>
              <LazyLoadImage src={Logo_img_4} alt="Format_3" loading="lazy" />
            </Frame>
          </FormatList>

          <Text size="26">
            Узнайте, какой формат подойдет <br /> именно для вашего продукта:
          </Text>
          <Button>Начать общение</Button>
        </InnerWrap>
      </SFormats>
    </ScrollWrapper>
  );
};

export default Formats;
